import React, {useEffect, useState} from 'react'
import SuperButton from '../hw04/common/c2-SuperButton/SuperButton'
import {restoreState, saveState} from '../hw06/localStorage/localStorage'
import s from './Clock.module.css'

function Clock() {
    const [timerId, setTimerId] = useState<number | undefined>(undefined)
    // for autotests //   //      ,     
    const [date, setDate] = useState<Date>(new Date(restoreState('hw9-date', Date.now())))
    const [show, setShow] = useState<boolean>(false)
    const [timerStart, setTimerStart] = useState(false)

    const start = () => {
        const timer = setInterval(() => {
            setDate(new Date)
        }, 500)
        setTimerId(Number(timer))
        setTimerStart(true)

        //   //   (   ,   +1)
        //    (https://learn.javascript.ru/settimeout-setinterval#setinterval)

    }

    const stop = () => {
        //   //    ,    (timerId <- undefined)
        /*alarm.pause();
        setAlarmTime("");*/
        saveState('hw9-date', new Date)
        clearInterval(timerId)
        setTimerStart(false)
    }

    const onMouseEnter = () => { //   //     
        setShow(true)
    }
    const onMouseLeave = () => { //   //      
        setShow(false)
    }

    const yyyy = date.getFullYear()
    let mm: number | string = date.getMonth() + 1
    let dd: number | string = date.getDate()

    if (mm < 10) mm = '0' + mm
    if (dd < 10) dd = '0' + dd

    const stringTime = `${date.toTimeString().split(' ')[0]}` || 'date->time' || <br/> // 24:: (01:02:03)/(23:02:03)/(24:00:00)/(00:00:01) //  
    const stringDate =  `${dd}.${mm}.${yyyy}` || 'date->date' || <br/> // .. (01.02.2022) //  ,  01.02.0123/01.02.-123/01.02.12345  

    //    ,    (https://learn.javascript.ru/intl#intl-datetimeformat)
    const stringDay = `${date.toLocaleDateString('en-EN', {weekday: 'long'})}` || 'date->day' || <br/> //  
    const stringMonth = `${date.toLocaleDateString('en-EN', {month: 'long'})}` || 'date->month' || <br/> //  

    return (
        <div className={s.clock}>
            <div
                id={'hw9-watch'}
                className={s.watch}
                onMouseEnter={onMouseEnter}
                onMouseLeave={onMouseLeave}
            >
                <span id={'hw9-day'}>{stringDay}</span>,{' '}
                <span id={'hw9-time'}>
                    <strong>{stringTime}</strong>
                </span>
            </div>

            <div id={'hw9-more'}>
                <div className={s.more}>
                    {show ? (
                        <>
                            <span id={'hw9-month'}>{stringMonth}</span>,{' '}
                            <span id={'hw9-date'}>{stringDate}</span>
                        </>
                    ) : (
                        <>
                            <br/>
                        </>
                    )}
                </div>
            </div>

            <div className={s.buttonsContainer}>
                <SuperButton
                    id={'hw9-button-start'}
                    disabled={timerStart} //   //    
                    onClick={start}
                >
                    start
                </SuperButton>
                <SuperButton
                    id={'hw9-button-stop'}
                    disabled={!timerStart} //   //     
                    onClick={stop}
                >
                    stop
                </SuperButton>
            </div>
        </div>
    )
}

export default Clock
